import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

const styles = {
  card: {
    maxWidth: 800,
    margin: 20,
  },
  media: {
    height: 0,
    paddingTop: '73.25%', // 16:9
  },
};

function SimpleMediaCard(props) {
  const { classes } = props;
  return (
    <div>
      <Card className={classes.card}>
        <CardMedia
          className={classes.media}
          image='./poolservice.jpg'
          title=""
        />
        <CardContent>
          <Typography gutterBottom variant="headline" component="h2">
          
          </Typography>

          <Typography component="p">
          The experts at Pinch A Penny can help you with all your pool care needs. We offer a variety of services including weekly cleaning and maintenance, equipment installation and repair, leak detection, and resurfacing. Our techs are fully trained to provide you with best-in- class service so you can enjoy the most out of your swimming pool.
          </Typography>
        </CardContent>
        <CardActions>
          <Button size="large" color="primary">
            Get a Quote
          </Button>
        </CardActions>
      </Card>
    </div>
  );
}

SimpleMediaCard.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(SimpleMediaCard);


