import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

const styles = {
  card: {
    maxWidth: 800,
    margin: 20,
  },
  media: {
    height: 0,
    paddingTop: '75.25%', // 16:9
  },
};

function SimpleMediaCard(props) {
  const { classes } = props;
  return (
    <div>
      <Card className={classes.card}>
        <CardMedia
          className={classes.media}
          image='./pc1.png'
          title=""          
        />
        <CardContent>
          <Typography gutterBottom variant="headline" component="h2">
           
          </Typography>
          <Typography component="p">
          Maintaining the Perfect Pool is as Easy as ABC!
You can count on Pinch A Penny to keep bacteria and algae from affecting your pool's sparkle. With the ABCs of pool maintenance and Suncoast chemicals, you'll have the crystal-clear, beautiful swimming pool you want!
          </Typography>
        </CardContent>
        <CardActions>
          <Button size="large" color="primary">
            View The Pool Care Guide
          </Button>
        </CardActions>
      </Card>
    </div>
  );
}

SimpleMediaCard.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(SimpleMediaCard);


